<style>
    div {
        width: 100%;
        height: 100%;
    }
    
    img {
        width: 100%;
    }
    
    h2 {
        text-align: center;
    }
</style>

<script>
    let m = {
        x: 0,
        y: 0
    };

    function handleMousemove(event) {
        m.x = event.clientX;
        m.y = event.clientY;
    }

    let src = 'homepage.png'
</script>


<div on:mousemove={handleMousemove}>
    <h2>Move mouse around page</h2>
    <img {src} alt="Mesosphere homepage"> The mouse position is {m.x} x {m.y}
</div>